import { Component, OnInit } from '@angular/core';
import {
  Platform,
  MenuController,
  ActionSheetController,
  ToastController,
} from '@ionic/angular';

import { Router } from '@angular/router';
import { LocalService } from '../../services/local.service';
import { RestService } from '../../services/rest.service';
import { UtilityService } from '../../services/utility.service';
import { EventsService } from '../../services/events.service';

import OneSignal from 'onesignal-cordova-plugin';

import { Camera, CameraResultType, CameraSource } from '@capacitor/camera';
import {
  FileTransfer,
  FileUploadOptions,
  FileTransferObject,
} from '@ionic-native/file-transfer/ngx';
import { DomSanitizer } from '@angular/platform-browser';


@Component({
  selector: 'app-cleanerregister',
  templateUrl: './cleanerregister.page.html',
  styleUrls: ['./cleanerregister.page.scss'],
})
export class CleanerregisterPage implements OnInit {
  cleaner: any = {
    name: '',
    email: '',
    phone: '',
    password: '',
    address: '',
    postcode: '',
  };
  verification = false;
  verificationCode = null;
  code: any = null;
  token: any = null;

  lastImage: any = null;
  imageToUpload: any = null;
  ext: any = null;

  cnfpassword: any = '';

  constructor(
    private platform: Platform,
    private menu: MenuController,

    public localService: LocalService,
    public restService: RestService,
    public utilityService: UtilityService,
    public eventsService: EventsService,
    private router: Router,
    public actionSheetCtrl: ActionSheetController,
    public toastCtrl: ToastController,
    private transfer: FileTransfer,
    private sanitizer: DomSanitizer
  ) {
    this.platform.ready().then(() => {
      OneSignal.setAppId(this.utilityService.config.onesignalAppId);

      OneSignal.getDeviceState((stateChanges) =>{
        console.log('login: ' + JSON.parse(JSON.stringify(stateChanges)).userId);
        this.token = JSON.parse(JSON.stringify(stateChanges)).userId;
        console.log("this.token",this.token);
      });
    })
  }

  ngOnInit() {}

  user() {
    this.router.navigate(['/signup']);
  }

  login() {
    this.router.navigate(['/cleanerlogin']);
  }

  register() {
    if (this.cleaner.name === '') {
      return this.utilityService.openAlerts('Error', 'Name required.', 'ok');
    }
    if (this.cleaner.email === '') {
      return this.utilityService.openAlerts('Error', 'Email required.', 'ok');
    }
    if (this.cleaner.phone === '') {
      return this.utilityService.openAlerts(
        'Error',
        'Phone number required.',
        'ok'
      );
    }
    if (this.cleaner.password.length < 5) {
      return this.utilityService.openAlerts(
        'Error',
        'Password should be minimum 5 character.',
        'ok'
      );
    }
    if (this.cleaner.password !== this.cnfpassword) {
      return this.utilityService.openAlerts(
        'Error',
        'Password & confirm password must be same.',
        'ok'
      );
    }
    if (this.cleaner.address === '') {
      return this.utilityService.openAlerts('Error', 'Address required.', 'ok');
    }
    if (this.cleaner.postcode === '') {
      return this.utilityService.openAlerts(
        'Error',
        'Postcode required.',
        'ok'
      );
    }

    // image also required
    if (!this.lastImage) {
      return this.utilityService.openAlerts(
        'Error',
        'Profile image required.',
        'ok'
      );
    }

    this.cleaner.device = this.getPlatform();
    this.cleaner.token = this.token;
    this.utilityService.loadingOpen('Registering...');
    this.restService.postData('cleaner/register', this.cleaner).subscribe(
      (data) => {
        this.utilityService.loadingClose();
        // now upload image of cleaner silently
        if (this.lastImage != null) {
          this.uploadImage(data.id);
        }
        // verify view
        //this.Verification = true;
        this.verificationCode = data.code;
        //
        this.cleaner = data;

        // no verification required =========
        this.utilityService.openAlerts('Registration Successful', 'You may login now.', 'ok');
        this.router.navigateByUrl('/cleanerlogin');
      },
      (err) => {
        this.utilityService.loadingClose();
        if (err.status === 403 && err.error.error.email) {
          return this.utilityService.openAlerts(
            'Error',
            'The email has already been taken.',
            'ok'
          );
        } else if (err.status === 404) {
          return this.utilityService.openAlerts(
            'Error',
            'Please enter a valid address & postcode.',
            'ok'
          );
        } else {
          return this.utilityService.openAlerts(
            'Error',
            'Cleaner registration failed, please try again later.',
            'ok'
          );
        }
      }
    );
  }

  verify() {
    if (this.cleaner.code === this.verificationCode) {
      // active the cleaner
      this.utilityService.loadingOpen('Verifying...');
      this.restService
        .postData('cleaner/verify', {
          email: this.cleaner.email,
          code: this.verificationCode,
        })
        .subscribe(
          (data) => {
            this.utilityService.loadingClose();
            this.utilityService.openAlerts('Done', 'You can now login.', 'ok');
            this.router.navigateByUrl('/cleanerlogin');
          },
          (err) => {
            this.utilityService.loadingClose();
            return this.utilityService.openAlerts(
              'Error',
              'Something not right at verification.',
              'ok'
            );
          }
        );
    } else {
      return this.utilityService.openAlerts(
        'Error',
        'Inavalid code entered.',
        'ok'
      );
    }
  }

  getPlatform() {
    if (this.platform.is('ios') || this.platform.is('ipad')) {
      return 'ios';
    } else if (this.platform.is('android')) {
      return 'android';
    } else {
      return 'other';
    }
  }

  terms() {
    this.router.navigate(['/terms']);
  }

  // image uploading section =========================
  async takePicture() {
    // Get the data of an image
    const image = await Camera.getPhoto({
      quality: 60,
      allowEditing: false,
      resultType: CameraResultType.Uri,
    });

    this.imageToUpload = image.path;
    this.lastImage = this.sanitizer.bypassSecurityTrustResourceUrl(
      image && image.webPath
    );
    this.ext = image.format;
  }

  async uploadImage(cleanerId) {
    const options = {
      fileKey: 'image',
      fileName: `file-${cleanerId}.${this.ext}`,
      chunkedMode: false,
      mimeType: 'multipart/form-data',
      params: {
        fileName: `file-${cleanerId}.${this.ext}`,
        image: `file-${cleanerId}.${this.ext}`,
      },
    };

    const fileTransfer: FileTransferObject = this.transfer.create();
    fileTransfer
      .upload(
        this.imageToUpload,
        this.restService.baseURL + 'cleaner/update_cleaner_image/' + cleanerId,
        options
      )
      .then(
        (data) => {
          console.log(data);
          // clear the last data
          this.lastImage = null;
          this.imageToUpload = null;
        },
        (err) => {
          console.log(err);
        }
      );
  }
}
